export default {
	data() {
		return {
			auth_time:6,
			getCodeText: '获取验证码',
			getOver: false,
			forgetData: {
				phone: '',
				code: '',
				password: ''
			}
		}
	},
	methods: {
		navTo(url) {
			this.$commonJs.navTo(url)
		},
		getCode() {
			var Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				}
			];
			let valLoginRes = this.$validate.validate(this.forgetData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.sendAuthCode = false;
			
			var auth_timetimer = setInterval(() => {
				this.auth_time--;
				this.getCodeText = this.auth_time + '后获取';
				this.getOver = true;
				if (this.auth_time <= 0) {
					this.sendAuthCode = true;
					this.getCodeText = 's后重新获取';
					this.getOver = false;
					clearInterval(auth_timetimer);
				}
			}, 1000);
		},
		getCode(){
			var that = this;
			this.$apiJs.getCode({
				phone:this.registerData.phone,
			}).then(res => {
				if(res){
					that.$commonJs.uniMsg('发送成功', 'success')
					var auth_timetimer = setInterval(() => {
						that.auth_time--;
						that.getCodeText = this.auth_time + '后获取';
						that.getOver = true;
						if (that.auth_time <= 0) {
							that.getCodeText = 's后重新获取';
							that.getOver = false;
							clearInterval(auth_timetimer);
						}
					}, 1000);
				}
			})
		},
		submitForm() {
			var that = this;
			let Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				},
				{
					name: 'code',
					type: 'required',
					errmsg: '请输入验证码'
				},
				{
					name: 'password',
					type: 'required',
					errmsg: '请输入密码'
				},
			]
			let valLoginRes = this.$validate.validate(this.forgetData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.loginLoading = true;
			setTimeout(function() {
				that.$commonJs.uniMsg('登录成功', 'success')
				that.loginLoading = false;
				// uni.redirectTo({
				// 	url:''
				// })
				console.log('跳转首页');
			}, 1000);
		},
		submitRegister() {
			let Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				},
				{
					name: 'code',
					type: 'required',
					errmsg: '请输入验证码'
				},
				{
					name: 'r_password',
					type: 'required',
					errmsg: '请输入密码'
				},
			]
			let valLoginRes = this.$validate.validate(this.registerData, Rules)
			this.registerLoading = true;
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.$commonJs.uniMsg('注册成功', 'success')
			this.registerLoading = false;
			this.type = 0;
		}

	},
	mounted() {

	}
}
